.main-header {
  width: 100%;
  height: 3rem;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background: #27660f;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
  padding: 0 1rem;
  z-index: 5;
}
/* ff0055  FE5F55   b71c1c*/
main {
  margin-top: 3rem;
}

@media (min-width: 768px) {
  .main-header {
    justify-content: space-between;
  }
}