/* Image */
.bg-image {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.bg-image-parallax {
  background-attachment: fixed;
}

.bg-image-overlay-dark::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(56, 51, 64, 0.6);
}

.img-place {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  text-align: center;
  overflow: hidden;
}

.img-place > img {
  width: 100%;
  height: 100%;
}

.img-place.w-auto > img {
  width: auto;
  max-width: 100%;
}

.img-place.h-auto > img {
  height: auto;
  max-height: 100%;
}

.img-place.custom-size > img {
  width: unset;
  height: unset;
}

.embed-video {
  width: 460px;
  max-width: 100%;
  height: 320px;
}

.iconic-sm {
  display: inline-block;
  margin: 0;
  padding: 0;
  width: 50px;
  height: 50px;
  overflow: hidden;
  text-align: center;
}

.iconic-sm.iconic-text {
  line-height: 50px;
  border-radius: 50%;
}

.iconic-sm.iconic-text>span {
  font-size: 24px;
  line-height: inherit;
}

.iconic-md {
  display: inline-block;
  margin: 0;
  padding: 0;
  width: 70px;
  height: 70px;
  overflow: hidden;
  text-align: center;
}

.iconic-md.iconic-text {
  line-height: 70px;
  border-radius: 50%;
}

.iconic-md.iconic-text>span {
  font-size: 32px;
  line-height: inherit;
}

.iconic-lg {
  display: inline-block;
  margin: 0;
  padding: 0;
  width: 100px;
  height: 100px;
  overflow: hidden;
